import React,{useEffect,useState} from 'react';
import { Provider } from 'react-redux';
import User from './UpdUser';

const Home = props => {
	let [user,setUser] = useState({});
	let Button = props.data && props.data.Button;
	useEffect(()=>{
		console.log("Inside Effect hook:")		
		let updUser =()=>props.data && props.data.updateUser && props.data.updateUser('ReactCmp');
		let unsubscribe = props.data && props.data.subscribeParent(()=>{
			let newUser = props.data.parentState();
			if(newUser.name){
				setUser(newUser);
			}
		});				
		return ()=>{
			unsubscribe();		
		};
	},[(props.data && props.data.subscribeParent)]);
	useEffect(()=>{
		if(props.data && typeof props.data.parentState == "function"){
			let newUser = props.data.parentState();
			setUser(newUser);
		}
	},[])
	
	return (
		<>
			<div>Sample out put from second app</div>
			<div>Name:{user.name}</div>
			<div id = 'btn'></div>
			{Button && <Button onClick = {()=>props.data.updateUser('NewUser')} lable = 'UpdateUser' />}
			<br/>
			<User {...user} updateUser = {props.data && props.data.updateUser} />
		</>
	)
};


export default Home;